.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    @extend %display-element-properties;
    justify-content: center;

    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;

    transition: transform $animation-duration, color $animation-duration;

    &:not(:nth-child(4n)) {
      margin-right: 48px;
    }
  }

  &__card:hover {
    color: #34568b;
    transform: scale(1.2);
  }
}
